* {
  box-sizing: border-box;
}
.app {
  canvas {
    border-radius: 10px;
  }
  overflow: hidden;
  width: 100%;
  max-height: 100vh;
  height: auto;
  padding: 10px;
  background: #999;
  display: grid;
  gap: 10px;
  //grid-auto-rows: minmax(150px, auto);
  grid-template-areas:
    "header header header"
    "leftFirst center rightFirst"
    "leftSecond center rightFirst"
    "leftSecond center rightSecond"
    "leftThird center rightThird"
    "footer footer footer";
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 40px 60px 1fr 60px 1fr 1fr;
  .myHeader {
    grid-area: header;
    background: #595959;
    border-radius: 10px;
    .header {
      text-align: center;
      font-size: 32px;
      color: #222;
      height: 40px;
      line-height: 40px;
      font-weight: bold;
      vertical-align: middle;
    }
  }
  .left-first {
    grid-area: leftFirst;
    .leftFirst {
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
  }
  .leftSecond {
    grid-area: leftSecond;
    // border: 1px solid green;
  }

  .leftThird {
    grid-area: leftThird;
    display: flex;
    justify-content: space-between;
    // border: solid 1px red;
    & > div {
      max-width: 49%;
      flex: 1 0 45%;
      // border: 1px solid green;
    }
  }

  .center {
    grid-area: center;
    border-radius: 10px;
    // border: 1px solid blue;
    // display: flex;
    // align-items: center;
  }

  .rightFirst {
    grid-area: rightFirst;
    background: #000;
    border-radius: 10px;
    overflow: auto;
    table {
      border-collapse: collapse;
      border: 1px solid #cccccc;
      width: 100%;
      color: #fff;

      caption {
        text-align: center;
        color: #cccccc;
        font-weight: bold;
      }
      th,
      td {
        text-align: center;
        border: 1px solid #cccccc;
        background-color: #8dc1a9;
      }
      tr:nth-child(odd) td {
        background: #dd6b66;
      }
      tr:nth-child(even) td {
        background: #759aa0;
      }
      tr:hover {
        background-color: #7289ab;
        td {
          background: transparent;
        }
      }
    }
  }

  .right-second {
    grid-area: rightSecond;
    .rightSecond {
      display: flex;
      justify-content: space-around;
    }
  }

  .rightThird {
    grid-area: rightThird;
    // border: 1px solid red;
    // display: flex;
    // align-items: center;
    // border-radius: 10px;
  }

  .footer {
    grid-area: footer;
    // border: solid 1px red;
    display: flex;
    justify-content: space-between;
    #qushi {
      max-width: 42%;
      flex: 1 0 40%;
      border-radius: 10px;
      background: #fff;
    }
    #piaoju {
      max-width: 42%;

      flex: 1 0 40%;
      border-radius: 10px;
      background: #fff;
    }
    .fapiao {
      max-width: 15%;

      flex: 1 0 15%;
      border-radius: 10px;
      background: #000;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        max-width: 100%;
        height: auto;
      }
    }
  }
  section {
    border: solid 1px green;
  }
}
